function setPlaceholders(){
    $("[data-placeholder2]").each(function (i,el) {
        let placeholder = $(el).attr("data-placeholder")
        let placeholder2 = $(el).attr("data-placeholder2")


        if(window.innerWidth>992){
            $(el).attr("placeholder",placeholder)
        } else{
            $(el).attr("placeholder",placeholder2)
        }
    })
}


$(".global-search__field input,.local-search__field input").on("input",function () {
    if($(this).val().length > 3){
        $(this).parents(".global-search,.local-search").addClass("_show-results")
    }
})

$(".global-search__clear,.local-search__clear").click(function () {
    $(this).siblings("input").val("")
    $(this).parents(".global-search,.local-search").removeClass("_show-results")
})

$(document).mouseup( function(e){
    let div = $( ".global-search,.local-search" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_show-results")){
            $(div).removeClass("_show-results")
        }
    }
});

$(".m-select__toggle").click(function () {
    $(this).parents(".m-select").toggleClass("_open")
})

$(document).mouseup( function(e){
    let div = $( ".m-select" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
        }
    }
});
